.Settings_cloud__1T5dt{
    min-height: 73px;
    background-color: #2f7e7e;
    border-radius: 25px 25px 25px 0;
    padding: 10px;
    position:relative;
}

.Settings_cloud__1T5dt:before{
    content: '';
    position: absolute;
    border: 10px solid transparent; border-right: 10px solid #2f7e7e; border-bottom: 10px solid #2f7e7e;
    left: -20px;
    top: 58px;
}

.Settings_cloud__1T5dt:after{
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius:50%;
    background: aquamarine;
    left: -30px;
    top: 48px;
}


.Settings_cloudAfter__1qCpF{
